define('bookshop/router', ['exports', 'ember', 'bookshop/config/environment'], function (exports, Ember, config) {

  'use strict';

  var Router = Ember['default'].Router.extend({
    location: config['default'].locationType
  });

  Router.map(function () {
    this.route('books');
    this.route('cart');
  });

  exports['default'] = Router;

});